.product {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  margin: 10px;
  padding: 20px;
  width: 100%;
  max-height: 400px;
  background-color: white;
  z-index: 1;
}

.product > img {
  max-height: 200px;
  width: 100%;
  align-items: center;
  object-fit: contain;
  margin-bottom: 15px;
}

.product__info {
  align-content: center;
  font-display: center;
  font-size: medium;
  font-stretch: normal;
  word-wrap: normal;
  text-align: left;
}

.product__price {
  align-content: center;
  margin-top: 5px;
}

.product__button {
  background: #f0c14b;
  border: 1px solid;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}

.product__rating{
  display: flex;
}